<template>
  <div id="stockpage">
    <div class="tac">
      <el-form
        style="width:100%"
        ref="form"
        :inline="true"
        :model="form"
        label-width="40px"
      >
        <el-form-item label="渠道">
          <el-select
            filterable
            style="width:100%"
            v-model="form.channel"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in channels"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="国家">
          <el-select
            filterable
            @change="changeOpt"
            style="width:100%"
            v-model="form.country"
            placeholder="请选择渠道"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in countrys"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="站点">
          <el-select
            filterable
            style="width:100%"
            v-model="form.site"
            placeholder="请选择站点"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in stores"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="品名">
          <el-select
            :collapse-tags="true"
            filterable
            style="width:100%"
            multiple
            v-model="productnames_"
            placeholder="请选择品名"
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in productnames"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SKU">
          <el-select
            style="width:100%"
            :collapse-tags="true"
            multiple
            v-model="skus_"
            placeholder="请选择SKU"
            filterable
          >
            <el-option value="all" label="全部"></el-option>
            <el-option
              v-for="(item, i) in skus"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="仓库">
          <el-select
            style="width:100%"
            v-model="warehouses_"
            placeholder="请选择仓库"
            filterable
          >
            <el-option
              v-for="(item, i) in warehouses"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" type="primary" @click="searchInfoFun"
            >搜索</el-button
          >
        </el-form-item>
      </el-form>
    </div>

    <div class="right_table">
      <el-table
        highlight-current-row
        ref="stockpageTable"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.3)"
        :data="tableData"
        style="width: 100%"
        @header-click="change"
        max-height="700"
      >
        <el-table-column label="每日库存盘点">
          <el-table-column
            label="序号"
            type="index"
            :index="indexMethod"
            width="60"
          ></el-table-column>
          <el-table-column prop="platform" label="渠道"> </el-table-column>
          <el-table-column prop="site" label="站点" width="60">
          </el-table-column>
          <el-table-column prop="country" label="国家" width="60">
          </el-table-column>
          <el-table-column prop="product_code" label="产品编码" width="120">
          </el-table-column>
          <el-table-column prop="product_name" label="品名" width="220">
          </el-table-column>
          <el-table-column prop="spu" label="spu" width="200">
          </el-table-column>
          <el-table-column prop="sku" label="sku" width="200">
          </el-table-column>
          <el-table-column prop="all_nums" label="总计"> </el-table-column>
          <el-table-column
            prop="on_warehouse_num"
            v-if="showCol"
            label="在库合计"
          >
          </el-table-column>
          <el-table-column prop="fba" v-if="showCol" label="FBA库存">
          </el-table-column>
          <el-table-column prop="fbm" v-if="showCol" label="FBM库存">
          </el-table-column>
          <el-table-column prop="on_way_nums" v-if="showCol" label="在途合计">
          </el-table-column>
          <el-table-column prop="on_way_fba" v-if="showCol" label="在途FBA">
          </el-table-column>
          <el-table-column prop="on_way_fbm" v-if="showCol" label="在途FBM">
          </el-table-column>
          <el-table-column prop="avg" label="十日均单"> </el-table-column>
          <el-table-column prop="expect_turnover" label="期望营业额">
          </el-table-column>
          <el-table-column prop="estimate_sell_out_days" label="预计售空天数">
          </el-table-column>
          <el-table-column
            prop="estimate_sell_out_date"
            label="预计售空日期"
            width="100"
          >
          </el-table-column>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="changePage"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total, prev, pager, next,jumper"
        :total="totalNum"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      // 分页
      currentPage: 1,
      totalNum: 0,
      // 默认显示总计后六列
      showCol: true,
      channels: [],
      stores: [],
      countrys: [],
      selOpts: [],
      warehouses: ["FBA"],
      productnames: [],
      skus: [],
      // 备份还原sku
      all_skus: [],
      // 多选，选中全部键
      warehouses_: ["FBA"],
      productnames_: ["all"],
      skus_: ["all"],

      //选中的值
      form: {
        channel: "all",
        site: "all",
        country: "all",
        product_name: ["all"],
        sku: ["all"],
        warehouses: ["all"]
      },
      tableData: []
    };
  },
  watch: {
    productnames_: {
      deep: true,
      handler: function(val, oldval) {
        let newindex = val.indexOf("all");
        let oldindex = oldval.indexOf("all");
        //获取val和oldval里all的索引,如果没有则返回-1
        if (
          (newindex != -1 && oldindex == -1 && val.length > 1) ||
          (newindex == -1 &&
            oldindex == -1 &&
            val.length === this.productnames.length)
        ) {
          //如果新的选择里有勾选了选择所有选择所有 则 只直线勾选所有整个选项
          this.productnames_ = ["all"];
        } else if (newindex != -1 && oldindex != -1 && val.length > 1) {
          //如果操作前有勾选了选择所有且当前也选中了勾选所有且勾选数量大于1  则移除掉勾选所有
          this.productnames_.splice(val.indexOf("all"), 1);
        }
        this.form.product_name = this.productnames_;
      }
    },
    skus_: {
      deep: true,
      handler: function(val, oldval) {
        let newindex = val.indexOf("all");
        let oldindex = oldval.indexOf("all");
        //获取val和oldval里all的索引,如果没有则返回-1
        if (
          (newindex != -1 && oldindex == -1 && val.length > 1) ||
          (newindex == -1 && oldindex == -1 && val.length === this.skus.length)
        ) {
          //如果新的选择里有勾选了选择所有选择所有 则 只直线勾选所有整个选项
          this.skus_ = ["all"];
        } else if (newindex != -1 && oldindex != -1 && val.length > 1) {
          //如果操作前有勾选了选择所有且当前也选中了勾选所有且勾选数量大于1  则移除掉勾选所有
          this.skus_.splice(val.indexOf("all"), 1);
        }
        this.form.sku = this.skus_;
      }
    }
  },
  methods: {
    searchInfoFun() {
      this.currentPage = 1;
      this.searchInfo();
    },
    changeOpt() {
      if (this.form.country !== "all") {
        let url = this._url2 + "server/personnel_management/get_sign_area/";
        this.$axios
          .get(url, {
            params: {
              country: this.form.country
            }
          })
          .then(res => {
            this.stores = res.data.data;
          });
      } else {
        this.stores = [];
        this.form.site = "all";
      }
    },
    changeStore(val) {},
    change(col) {
      if (col.label === "总计") {
        this.showCol = !this.showCol;
      }
    },
    // 请求渠道，站点，国家 关联的搜索参数
    getRelativeParams(url, queryParams) {
      this.$axios
        .get(url, {
          params: queryParams
        })
        .then(res => {
          if (queryParams.name === "platform") {
            // 渠道请求，渲染站点
            console.log(res);
            this.stores = res.data.data;
          } else if (queryParams.name === "site") {
            this.countrys = res.data.data;
          } else if (queryParams.name === "country") {
            this.skus = res.data.data;
          }
        });
    },

    // 进来请求搜索参数
    getSearchParams() {
      let url = this._url2 + "server/databases/get_search/";
      this.$axios
        .get(url)
        .then(res => {
          console.log(res);
          this.productnames = res.data.product_name_list;
          this.skus = res.data.sku_list;

          // 深拷贝一份，用于恢复全部的sku列表
          let objStr = JSON.stringify(this.skus);
          this.all_skus = JSON.parse(objStr);
        })
        .catch(err => {});
      let url1 = this._url2 + "server/personnel_management/get_sign_country/";
      // let url1 = this._url2 + "server/personnel_management/get_sign_country/";
      this.$axios.get(url1).then(res => {
        this.channels.push(res.data.data1);

        this.countrys = this.changeCountry(res.data.data);
      });
    },
    changeCountry(data = []) {
      let flag = false;
      let countryList = [];
      for (let i = 0; i < data.length; i++) {
        let item = data[i];
        if (
          item === "法国" ||
          item === "德国" ||
          item === "意大利" ||
          item === "西班牙"
        ) {
          flag = true;
          continue;
        }
        countryList.push(item);
      }
      if (flag) {
        countryList.push("欧洲");
      }
      return countryList;
    },
    searchInfo() {
      // 发送搜索请求，如果没有请求参数，则访问全部数据
      // let pro_name = this.form.productnames.join();
      // let ware_houses = this.form.warehouses.join();
      // let skus = this.form.skus.join();
      // if (ware_houses === "all") {
      //   // 拼接所有字段
      //   ware_houses = this.warehouses.join();
      // }
      // // 将数组拼接成数据
      // console.log(pro_name, ware_houses, skus);
      this.loading = true;
      let paramsObj = [];
      console.log(this.form);
      for (let key in this.form) {
        console.log(key, this.form[key]);

        let item = this.form[key];
        // value :Amazon  key :channel

        if (
          item.indexOf("all") > -1 ||
          !item ||
          (typeof item === "object" && item.length < 1)
        ) {
          continue;
        }
        let obj = {};
        let value;
        if (item instanceof Array) {
          console.log("sku", item);
          if (item.indexOf("all") === -1) {
            value = item.join();
          }
        } else {
          value = item;
        }
        if (key === "channel") {
          obj["key"] = "platform";
          obj["value"] = value;
        } else {
          obj["key"] = key;
          obj["value"] = value;
        }
        paramsObj.push(obj);
      }
      console.log("请求参数", paramsObj);
      let url = this._url2 + "server/databases/get_stock_data/";
      console.log(paramsObj);
      this.$axios
        .get(url, {
          params: {
            data: JSON.stringify(paramsObj),
            page: this.currentPage
          }
        })
        .then(res => {
          this.loading = false;
          console.log(res);
          this.tableData = res.data.data;
          this.totalNum = res.data.total_num;
          this.$nextTick(() => {
            this.$refs["stockpageTable"].bodyWrapper.scrollTop = 0;
          });
        });
    },
    getAllData() {
      this.loading = true;
      let url = this._url2 + "server/databases/get_stock_data/";
      this.$axios.get(url).then(res => {
        console.log(res);
        this.tableData = res.data.data;
        this.totalNum = res.data.total_num;
        this.loading = false;
      });
    },
    changePage(val) {
      this.currentPage = val;
      // 发请求
      this.searchInfo();
    },
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    }
  },
  created() {
    // 获取下拉数据
    this.getSearchParams();
    // 获取全部数据
    this.getAllData();
  }
};
</script>
<style scoped>
#stockpage >>> .el-table--border td:first-child .cell {
  padding-left: 0;
}
</style>
<style>
#stockpage .tac {
  padding-top: 22px;
  text-align: center;
}
#stockpage .el-col.el-col-21 {
  padding: 0 50px;
}
#stockpage .el-table .cell {
  text-align: center;
}
#stockpage .el-col.el-col-20 {
  padding: 0 30px;
}
#stockpage .el-form-item__label {
  color: #fff;
}
#stockpage .el-pagination {
  background: #fff;
  text-align: center;
}
</style>



